@font-face {
  font-family: "the_perfect_christmasregular";
  src: url("the_perfect_christmas-webfont.woff2") format("woff2"),
    url("the_perfect_christmas-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "merry_christmas_flakeregular";
  src: url("merrychristmasflake-webfont.woff2") format("woff2"),
    url("merrychristmasflake-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "merry_christmas_starregular";
  src: url("merrychristmasstar-webfont.woff2") format("woff2"),
    url("merrychristmasstar-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  background-color: #cc0000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
